import React, { FC, HTMLAttributes, useEffect, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';

//styles
import './styles.scss';
//components
import { CardItem } from '../../Card';
import { Btn } from '../../Button';
//img
import { 
  noImage,
} from '../../../assets/img';
//constants
import { ROUTES } from '../../../constants';
// context
import { useCommon } from '../../../context';

interface HomeEventProps extends HTMLAttributes<HTMLDivElement> {}

export const HomeEvent: FC<HomeEventProps> = () => {
  const navigate = useNavigate()
  const common = useCommon()

  const [loading, setLoading] = useState<boolean>(true)

  const events = useMemo(
    () => common.upComingEvents.slice(0, 8),
    [common.upComingEvents]
  )

  useEffect(() => {
    getEvents()
  }, [])

  const getEvents = async () => {
    if (common.upComingEvents.length === 0) await common.getEvents('live', 'asc')

    setLoading(false)
  }

  const goToEventsPage = () => {
    navigate(ROUTES.event);
  }

  return (
    <div className='homeEvent'>
      <div className='homeEvent__header'>
        <span className='homeEvent__headerTitle'>Upcoming Events</span>
      </div>
      {
        loading === true ?
          (
            <div className='homeEvent__container mt16'>
              {
                Array(8).fill(0, 0, 8).map((_, index) => (
                  <CardItem key={index} type='nodata'/>
                ))
              }
            </div>
          )
          :
          (
            <div className='homeEvent__container mt16'>
              {
                events.map((item, index) => (
                  <CardItem 
                    key={index}
                    img={item.logo?.url || noImage}
                    title={item.name.text}
                    desc={item.description.text}
                  />
                ))
              }
            </div>
          )
      }
      <div className='homeEvent__footer mt48'>
        <Btn 
          text='View all events' 
          onClick={goToEventsPage}
        />
      </div>
    </div>
  );
};

