import React, { FC, HTMLAttributes, useEffect, useMemo, useState } from 'react';
import { NavLink, useLocation, useNavigate } from 'react-router-dom';

//styles
import './styles.scss';
//components
import { CardItem } from '../../Card';
import { Btn } from '../../Button';
//img
import { 
  noImage,
} from '../../../assets/img';
// context
import { useCommon } from '../../../context';

interface WholeEventsProps extends HTMLAttributes<HTMLDivElement> {}

export const WholeEvents: FC<WholeEventsProps> = () => {
  const [loading, setLoading] = useState<boolean>(true)

  const common = useCommon()

  const events = useMemo(
    () => common.allEvents,
    [common.allEvents]
  )

  useEffect(() => {
    getEvents()
  }, [])

  const getEvents = async () => {
    if (common.allEvents.length === 0) await common.getEvents('all', 'asc')

    setLoading(false)
  }
  
  return (
    <div className='wholeEvent'>
      <div className='wholeEvent__header'>
        <span className='wholeEvent__headerTitle'>All Events</span>
      </div>
      {
        loading === true ?
          (
            <div className='homeEvent__container mt16'>
              {
                Array(8).fill(0, 0, 8).map((_, index) => (
                  <CardItem key={index} type='nodata'/>
                ))
              }
            </div>
          )
          :
          (
            <div className='wholeEvent__container mt16'>
              {
                events.map((item, index) => (
                  <CardItem 
                    key={index}
                    img={item.logo?.url || noImage}
                    title={item.name.text}
                    desc={item.description.text}
                  />
                ))
              }
            </div>
          )
      }
    </div>
  );
};

