import { useCallback, useState } from 'react';
//axios
import axiosApiInstance from '../../api/axios';

export function useCommonProvider() {
  const [loading, setLoading] = useState<boolean>(false);
  const [upComingEvents, setUpComingEvents] = useState<any[] | []>([]);
  const [allEvents, setAllEvents] = useState<any[] | []>([]);

  const getEvents = async (state: string, sort: string) => {
    try {
      const getOrganizations = await axiosApiInstance.get(
        `v3/users/me/organizations/?token=UTIKHX2YPJJP637CLGPV`
      );

      const { data } = await axiosApiInstance.get(
        `v3/organizations/${getOrganizations.data.organizations[0].id}/events/?token=UTIKHX2YPJJP637CLGPV&status=${state}&show_series_parent=true&order_by=start_${sort}`
      );

      if (state === 'live') {
        setUpComingEvents(data.events);
      }

      if (state === 'all') {
        setAllEvents(data.events);
      }
      
    } catch (e) {
      console.log('getEvents error', e);
    }
  };

  return {
    setLoading,
    loading,
    getEvents,
    upComingEvents,
    allEvents,
    
  };
}
