import React, { FC, HTMLAttributes, useEffect, useState } from 'react';
import { Button } from "@chakra-ui/button"

//styles
import './styles.scss';

interface ButtonProps extends HTMLAttributes<HTMLDivElement> {
  text?: string;
  onClick?: () => void;
}

export const Btn: FC<ButtonProps> = ({ text, onClick }) => {

  const handle = () => {
    onClick === undefined ?
      console.log('')
      :
      onClick()
  }

  return (
    <Button 
      className='button'
      onClick={() => handle()}
    >
      {text}
    </Button>
  );
};

