import React, { FC, HTMLAttributes, useEffect, useState } from 'react';
import { NavLink, useLocation, useNavigate } from 'react-router-dom';

//styles
import './styles.scss';
import { 
  noImage,
} from '../../assets/img';

interface CardItemProps extends HTMLAttributes<HTMLDivElement> {
  img?: string;
  title?: string;
  desc?: string;
  type?: 'fill' | 'nodata';
}

export const CardItem: FC<CardItemProps> = ({ 
  img = noImage, 
  title = "", 
  desc = "", 
  type = 'fill' 
}) => {
  const imgSrc = type === 'fill' ? img : noImage

  return (
    <div className={type === 'fill' ? 'cardItem' : 'noHoverCardItem'}>
      <img
        src={imgSrc}
        className="cardItem__img"
        alt='card'
      />
      <div className='cardItem__textPanel'>
        <div>
          {
            type === 'fill' ? 
              (<span className='cardItem__title'>{title}</span>)
              :
              (<p className='cardItem__noDataTitle'></p>)
          }
        </div>
        <div>
          {
            type === 'fill' ? 
              (<span className='cardItem__desc'>{desc}</span>)
              :
              (
                <>
                  <p className='cardItem__noDataDesc'></p>
                  <p className='cardItem__noDataDesc mt8'></p>
                </>
              )
          }
        </div>
      </div>
    </div>
  );
};

