import React from 'react';
import { Routes, Route } from 'react-router-dom';
import { Helmet } from 'react-helmet';

//styles
import './scss/style.scss';
//constants
import {
  publicRoutes,
} from './constants';
//layouts
import {
  HomeLayout,
} from './layouts';

function App() {
  return (
    <>
      <Helmet>
        <title>Reunion</title>
      </Helmet>
      <Routes>
        <Route element={<HomeLayout />}>
          {publicRoutes.map((route) => (
            <Route
              path={route.name}
              element={route.component}
              key={route.name}
            />
          ))}
        </Route>
      </Routes>
    </>
  );
}

export default App;
